<template>
  <div id="set">
    <!-- 账号 -->
    <div class="set1">
      <h3>管理我的账号</h3>
      <div class="set-account">
        <div class="set-account_l" v-if="userInfo">
          <van-image :src="imgUrl" round fit="cover" />
          <!-- enctype="multipart/from-data"
            method="post"
            action -->
          <div class="set_from">
            <!-- accept="img/*"不限制图像格式 -->
            <!-- @input="iq" -->
            <!--   @change="uploadPicture" -->
            <input
              type="file"
              name="file"
              accept="img/*"
              ref="files"
              @change="uploadPicture"
            />
          </div>
          <!-- 编辑的图标 -->
          <img
            class="set_edit"
            src="data:image/png;base64,"
            alt=""
          />
        </div>
        <div class="set-account_r" v-if="userInfo">
          <h3>{{ userInfo.nickname }}</h3>
          <p>绑定手机号：{{ userInfo.phone }}</p>
        </div>
        <!-- 当前账号 -->
        <div class="set_current">当前账号</div>
      </div>
    </div>
    <!-- 信息 -->
    <div class="set2" v-if="userInfo">
      <div class="set_name">
        <span>昵称</span><input type="text" v-model="username" />
      </div>
      <div class="set_id">
        <span>ID号</span
        ><span>{{ userInfo.uid }}<i class="iconfont icon-lock"></i></span>
      </div>
      <div class="set_phone">
        <span>手机号码</span
        ><span>{{ userInfo.phone }}<i class="iconfont icon-lock"></i></span>
      </div>
      <router-link tag="div" class="set_pass" to="/change_password">
        <span>密码</span><span>点击修改密码<van-icon name="arrow" /></span>
      </router-link>
    </div>
    <!-- end -->
    <div class="set_btn">
      <button @click="save">保存修改</button>
      <button @click="end">退出登录</button>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      userInfo: null,
      // 上传的头像路径
      imgUrl: null,
      // 用户昵称
      username: "",
    };
  },
  created() {
    let token = this.$cookie.getCookie("token");
    axios({
      method: "GET",
      url: "http://47.115.51.185/api/user",
      headers: {
        "Authori-zation": "Bearer " + token,
      },
      data: {
        token: token,
      },
    }).then((res) => {
      this.userInfo = res.data.data;
      this.imgUrl = res.data.data.avatar;
      this.username = res.data.data.nickname;
      // console.log(this.userInfo);
    });
  },
  methods: {
    onSubmit(val) {
      console.log("表单", val);
    },
    // 上传头像，获取base64位的图片信息
    iq(e) {
      let file = e.target.files[0];
      let reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onloadend = () => {
        // base64格式
        let dataURL = reader.result;
        this.imgUrl = dataURL;
        console.log(this.imgUrl);
      };
    },
    // 把头像上传到服务器
    uploadPicture(e) {
      let file = e.target.files[0];
      let reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onloadend = () => {
        // base64格式
        let dataURL = reader.result;
        // this.axios.get("/api/upload/image", { file: dataURL }).then((res) => {
        //   console.log(res);
        // });
        axios({
          method: "POST",
          url: "http://47.115.51.185/api/upload/image",
          withCredentials: false,
          data: {
            file:dataURL,
          },
        }).then(res=>{
          console.log(res);
        })
      };
      // this.axios.get("/api/upload/image").then((res) => {
      //   console.log(res);
      // });
    },
    // /api/user/edit post请求
    // 点击确认保存
    save() {
      console.log(this.username);
      this.axios
        .post("/api/user/edit", {
          nickname: this.username,
          avatar:
            "http://kaifa.crmeb.net/uploads/attach/2019/08/20190807/723adbdd4e49a0f9394dfc700ab5dba3.png",
        })
        .then((res) => {
          console.log(res);
          this.$toast({
            type:"success",
            message:'修改成功',
            duration:"300",
            onClose:()=>{
              this.$router.push("/mine")
            }
          });
        });
    },
    // 退出登录
    end() {
      this.$dialog
        .confirm({
          message: "确认是否退出登录",
        })
        .then(() => {
          // 删除cookie的time和token
          this.$cookie.delCookie("token");
          this.$cookie.delCookie("time");
          this.axios.get("/api/logout").then((res) => {
            console.log(res);
            this.$router.push("/");
          });
        })
        .catch(() => {
          // console.log("已取消");
        });
    },
  },
};
</script>
<style lang="less" scoped>

#set {
  width: 100vw;
  // height: 100vh;
  background-color: #f5f5f5;
  //一
  .set1 {
    background: #fff;
    padding: 0.5rem 0 0.2rem 0;
    h3 {
      width: 9rem;
      text-align: left;
      margin: 0 auto;
      margin-bottom: 0.3rem;
      font-size: 0.32rem;
      color: #282828;
    }
    .set-account {
      width: 9rem;
      // height: 1.6rem;
      background-color: #fff9f9;
      background: url("data:image/png;base64,")
        no-repeat 0 0 / cover;
      margin: 0 auto;
      border: 1px solid #e93323;
      border-radius: 0.22rem;
      display: flex;
      align-items: center;
      padding: 0.3rem;
      margin-bottom: 0.22rem;
      position: relative;
      // 头像
      .set-account_l {
        width: 1.6rem;
        height: 1.6rem;
        position: relative;
        overflow: hidden;
        position: relative;
        .van-image {
          width: 100%;
          height: 100%;
        }
        // 上传头像
        .set_from {
          width: 100%;
          height: 100%;
          position: absolute;
          top: 0;
          left: 0;
          right: 0;
          bottom: 0;
          opacity: 0;
          z-index: 9;
          input {
            width: 100%;
            height: 100%;
          }
          .pic{
            width: 60px;
            height: 60px;
            // background-color: tomato;
            border-radius: 30px;
            background-repeat: no-repeat;
            background-size: cover;
          }
        }
        // 编辑
        .set_edit {
          width: 0.4rem;
          height: 0.4rem;
          border-radius: 50%;
          position: absolute;
          right: 0.05rem;
          bottom: 0.05rem;
        }
      }
      // 右
      .set-account_r {
        // flex: 1;
        text-align: left;
        width: calc(9rem - 1.6rem - 0.6rem);
        position: relative;
        margin-left: 0.2rem;
        display: flex;
        flex-wrap: wrap;
        align-content: center;
        h3 {
          width: 100%;
          font-size: 0.36rem;
          color: #282828;
        }
        p {
          font-size: 0.28rem;
          color: #999;
          margin-top: 0.1rem;
        }
      }
      .set_current {
        position: absolute;
        right: 0;
        top: 0;
        font-size: 0.26rem;
        background-color: rgba(233, 51, 35, 0.1);
        color: #fc4141;
        padding: 0.1rem 0.2rem;
        border-radius: 0 0.2rem 0 0.2rem;
      }
    }
  }
  //二
  .set2 {
    background: #fff;
    margin-top: 0.2rem;
    &.set2 > div {
      padding: 0.3rem 0.3rem 0.3rem 0;
      border-bottom: 1px solid #f2f2f2;
      margin-left: 0.3rem;
      font-size: 0.32rem;
      color: #282828;
      display: flex;
      align-items: center;
      position: relative;
      input {
        flex: 1;
        padding: 0 0;
        margin: 0 0;
        border: none;
        box-sizing: border-box;
        // padding-left: 2rem;
        text-align: right;
        color: #868686;
      }
      span:nth-child(2) {
        color: #868686;
        flex: 1;
        text-align: right;
        i {
          margin-left: 0.15rem;
        }
      }
    }
  }
  // 结束
  .set_btn {
    button {
      font-size: 0.42rem;
      display: block;
      width: 6.9rem;
      height: 1rem;
      text-align: center;
      color: #fff;
      border-radius: 0.5rem;
      line-height: 1rem;
      margin: 0.76rem auto 0 auto;
      border: none;
    }
    button:nth-child(1) {
      background: #e93323 !important;
    }
    button:nth-child(2) {
      background: transparent;
      color: #ff3700 !important;
      border: 1px solid #e93323 !important;
    }
  }
}
</style>